<template>
  <div>
    <section class="p-5" id="video">
      <div class="container">

        <BigTitle>Benefits of Attending</BigTitle>

        <div class="embed-responsive embed-responsive-16by9">
          <iframe width="100%" height="560" src="https://www.youtube.com/embed/cmdRxmFEbg8" frameborder="0"
                  allowfullscreen class="embed-responsive"></iframe>
        </div>

      </div>

    </section>

    <section class="py-5" id="benefits">
      <div class="container">

        <div class="row no-gutters">
          <ZigzagBox>
            <div slot="title">Advanced Your Skills</div>
            <div slot="description">
              More than half of the sessions at DevSummit are dedicated to software engineering and its integration ecosystem. The keynotes
              and sessions at DevSummit go beyond product announcements to make sure you leave with actionable ways to
              build better software.
            </div>
          </ZigzagBox>
        </div>

        <div class="row no-gutters">
          <div class="col-12 col-lg-6 order-2 order-lg-1 d-none d-md-block">
            <img
              src="https://images.unsplash.com/photo-1478812954026-9c750f0e89fc?dpr=1&auto=format&fit=crop&w=2850&h=&q=60&cs=tinysrgb&crop="
              alt="" class="img-fluid">
          </div>
          <div class="col-12 col-lg-6 order-1 order-lg-2 p-5 h-100 d-flex align-self-center">
            <div>
              <div class="h1 text-uppercase text-left">Build your community</div>
              <div class="text-secondary">
                <p>
                  DevSummit is designed to make sure you have plenty of opportunities to connect with developers who share your passions. From
                  student meet-ups to open source social hours to roundtable discussions on work, opportunities to build
                  your community are central to the conference.
                </p>
              </div>
            </div>
          </div>
        </div>

        https://images.unsplash.com/photo-1493689485253-f07fcbfc731b?dpr=1&auto=format&fit=crop&w=2833&h=&q=60&cs=tinysrgb&crop=
        https://images.unsplash.com/photo-1493612276216-ee3925520721?dpr=1&auto=format&fit=crop&w=2800&h=&q=60&cs=tinysrgb&crop=

      </div>
    </section>
  </div>
</template>

<script>
  export default {
    name: 'WhyAttend',

    components: {
      ZigzagBox: () => import('@/components/ZigzagBox')
    }
  }
</script>

<style lang="scss" scoped>
  section {
    color: #444;
    line-height: 1.8rem;
    background: #ffffff;
  }
</style>



